import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, ReactiveFormsModule, Validators} from '@angular/forms';
import {TaxCalcService} from "../services/tax-calc.service";
import {MatFormFieldModule} from "@angular/material/form-field";
import {MatIconModule} from "@angular/material/icon";
import {CommonModule} from "@angular/common";
import {MatInputModule} from "@angular/material/input";
import {MatButtonModule} from "@angular/material/button";

@Component({
  selector: 'app-tax-return-calculator',
  standalone: true,
  templateUrl: './tax-return-calculator.component.html',
  imports: [
    MatFormFieldModule,
    ReactiveFormsModule,
    MatIconModule,
    CommonModule,
    MatInputModule,
    MatButtonModule,
  ],
  styleUrls: ['./tax-return-calculator.component.css']
})
export class TaxReturnCalculatorComponent implements OnInit {

  taxReturnForm = new FormGroup({
    totalIncome: new FormControl(0, Validators.required),
    nonTaxableIncome: new FormControl(0, Validators.required),
  });

  totalTax: number;
  actualTax: number;
  saving: number;

  constructor(
    private taxService: TaxCalcService
  ) { }


  ngOnInit(): void {
  }

  getFormValue(name: string): any{
    return this.taxReturnForm.get(name).value;
  }

  setFormValue(name: string, value: any): void{
    this.taxReturnForm.get(name).setValue(value);
  }

  calculate(): void{
    this.totalTax = undefined;
    this.actualTax = undefined;
    this.saving = undefined;
    if (this.taxReturnForm.valid){
      const totalIncome = this.taxReturnForm.get('totalIncome').value as number;
      const actualIncome = totalIncome - (this.taxReturnForm.get('nonTaxableIncome').value as number);
      const totalAfterTaxIncome = this.taxService.calculateAfterTax(totalIncome);
      const actualAfterTaxIncome = this.taxService.calculateAfterTax(actualIncome);
      this.totalTax = totalIncome - totalAfterTaxIncome;
      this.actualTax = actualIncome - actualAfterTaxIncome;
      this.saving = this.totalTax - this.actualTax;
    }
  }
}
