import { Component, OnInit } from '@angular/core';
import {MatFormFieldModule} from "@angular/material/form-field";
import {FormsModule} from "@angular/forms";
import {MatIconModule} from "@angular/material/icon";
import {CommonModule} from "@angular/common";
import {MatInputModule} from "@angular/material/input";

@Component({
  selector: 'app-string-refactor',
  standalone: true,
  templateUrl: './string-refactor.component.html',
  imports: [
    MatFormFieldModule,
    FormsModule,
    MatIconModule,
    CommonModule,
    MatInputModule
  ],
  styleUrls: ['./string-refactor.component.css']
})
export class StringRefactorComponent implements OnInit {

  inputValue: string;

  outputValue: string;

  constructor() { }

  ngOnInit(): void {
  }

  processValue(): void{
    if (this.inputValue?.length){
      const inputArr = this.inputValue.split('-');
      this.outputValue = inputArr.reduce((acc, newValue) => {
        return this.capitalize(acc) + '.' + this.capitalize(newValue);
      });
      const date = new Date();
      this.outputValue += '.' + date.getFullYear() + '.' + date.getMonth();
    }
  }

  capitalize(value: string){
    if (value.length){
      return value.charAt(0).toUpperCase() + value.slice(1);
    }
    return value;
  }
}
