import {Component, computed, OnInit, signal} from '@angular/core';
import {MatFormFieldModule} from "@angular/material/form-field";
import {MatIconModule} from "@angular/material/icon";
import {FormsModule} from "@angular/forms";
import {ClipboardModule} from "@angular/cdk/clipboard";
import {CommonModule} from "@angular/common";
import {MatInputModule} from "@angular/material/input";
import {MatButtonModule} from "@angular/material/button";
import {MatSelectModule} from "@angular/material/select";

const PUBLISHERS = [
  "O'Reilly",
  "Manning",
  "Apress",
  "packt",
  "Wiley",
  "Springer",
  "CRCPress",
  "NoStarchPress",
  "Pearson",
  ""
]

@Component({
  selector: 'app-string-assembler',
  standalone: true,
  templateUrl: './string-assembler.component.html',
  styleUrls: ['./string-assembler.component.css'],
  imports: [
    MatFormFieldModule,
    MatIconModule,
    FormsModule,
    ClipboardModule,
    CommonModule,
    MatInputModule,
    MatButtonModule,
    MatSelectModule,
  ]
})
export class StringAssemblerComponent implements OnInit {

  inputValue: string;

  inputYear: string;

  inputMonth: string;

  inputEdition = signal<string | undefined>(undefined);

  computedInputEdition = computed(() => {
    if(this.inputEdition()){
      const value = this.inputEdition();
      if(value.length >= 2){
        if(value[value.length - 2] === '1'){
          return `${value}th`
        }
      }
      switch(value[value.length - 1]){
        case '1':
          return `${value}st`;
        case '2':
          return `${value}nd`;
        case '3':
          return `${value}rd`;
      }
      return `${value}th`;
    }
    return undefined;
  });

  inputPublisher: string;

  selectedPublisher: string;

  outputValue: string;

  publishers: string[]

  constructor() {
    this.publishers = PUBLISHERS;
  }

  ngOnInit(): void {
    const date = new Date();
    this.inputYear = date.getFullYear().toString();
    this.inputMonth = (date.getMonth() + 1).toString();
  }

  appendPublisher(): void {
    const val = this.inputPublisher.trim();
    if(!this.publishers?.includes(val)){
      this.publishers.push(val);
    }
    this.inputPublisher = "";
  }

  processValue(): void{
    this.outputValue = [
      (this.inputValue ?? "").replaceAll("-", " ").replaceAll("#", "Sharp").replaceAll(".",'Dot').replaceAll(":", " "),
      this.computedInputEdition(),
      !!this.computedInputEdition() ? "Edition" : "",
      this.selectedPublisher,
      this.inputYear,
      this.inputMonth
    ].filter((v) => v && v.length > 0)
      .join("-")
  }
}
