import { Component, OnInit } from '@angular/core';
import {routes} from '../app.config';
import {Route, RouterLink} from '@angular/router';
import {MatCardModule} from "@angular/material/card";
import {CommonModule} from "@angular/common";


interface ToolingCard {
  name: string;
  path: string;
  order?: number;
  imageUri?: string;
}


@Component({
  selector: 'app-home-page',
  standalone: true,
  templateUrl: './home-page.component.html',
  imports: [
    MatCardModule,
    RouterLink,
    CommonModule,
  ],
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  toolingCardCollection: ToolingCard[];
  constructor() { }

  ngOnInit(): void {

    const validRoutes = routes.filter((route: Route) => (route.data));

    this.toolingCardCollection = validRoutes.map((route: Route) => {
      if (route.data){
        const toolingCard: ToolingCard = {
          name: route.data.cardName,
          path: route.path,
        };
        if (route.data.order){
          toolingCard.order = route.data.order;
        }
        if (route.data.imageUri){
          toolingCard.imageUri = route.data.imageUri;
        }
        return toolingCard;
      }
    });
  }

}
