import {provideRouter, Routes} from "@angular/router";
import {ApplicationConfig} from "@angular/core";
import {IncomeCalculatorComponent} from "./income-calculator/income-calculator.component";
import {LoanCalculatorComponent} from "./loan-calculator/loan-calculator.component";
import {StringRefactorComponent} from "./string-refactor/string-refactor.component";
import {TaxReturnCalculatorComponent} from "./tax-return-calculator/tax-return-calculator.component";
import {StringAssemblerComponent} from "./string-assembler/string-assembler.component";
import {HomePageComponent} from "./home-page/home-page.component";
import {provideAnimations} from "@angular/platform-browser/animations";

export const routes: Routes = [
  {path: 'income', component: IncomeCalculatorComponent, data: {
      navBarName: 'Income',
      cardName: 'Income Calculator',
      order: 1
    } },
  {path: 'loan', component: LoanCalculatorComponent, data: {
      navBarName: 'Loan',
      cardName: 'Loan Calculator',
      order: 2
    }},
  {path: 'string-refactor', component: StringRefactorComponent, data: {
      cardName: 'String Refactor',
      order: 3
    }},
  {path: 'tax-return-1', component: TaxReturnCalculatorComponent, data: {
      cardName: 'Tax Return Calculator 1',
      order: 4
    }},
  {path: 'string-assembler', component: StringAssemblerComponent, data: {
      cardName: 'String Assembler',
      order: 5
    }},
  {path : '', component: HomePageComponent},
  {path: '**', component: HomePageComponent}
];

export const appConfig: ApplicationConfig = {
  providers: [provideRouter(routes), provideAnimations()]
};
